import React from 'react';
import styled from 'styled-components';

const Input = styled.input`
  display: block;
  border-radius: 0px;
  border: 1px solid #aaa;
  padding: 0.25rem;
  outline: none;
  width: 100%;
  box-sizing: border-box;
`;

export default Input;


